* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

h1, h2, h3, h4, h5, h6, ul, p, b {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: none;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.search-form {
  background-color: azure;
  border-bottom: 1px solid green;
  justify-content: center;
  padding: 15px 0;
  display: flex;
}

.search-form input {
  width: 350px;
  height: 30px;
  border: none;
  border-radius: 4px;
  outline: 1px solid #48ff0052;
  padding-left: 10px;
}

.search-form input:hover, .search-form input:focus {
  background-color: #48ff0052;
}

.search-form button {
  outline: 1px solid #48ff0052;
}

.search-form button:hover {
  background-color: #48ff0052;
  outline: 1px solid #48ff0052;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  width: 280px;
  color: gray;
  border-radius: 1.5px;
  outline: .1px solid #00000031;
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

.photo-card:hover, .photo-card.focus {
  color: #000;
  background-color: #0000000c;
}

.photo-card img {
  width: 100%;
}

.info {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.load-more {
  width: 100px;
  height: 40px;
  background-color: #8d8d8d;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more:hover {
  background-color: #48ff0052;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

